@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700);

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.about-me {
  display: grid;
}

.about-me-box1{
  display:grid;
  grid-template-columns: 2fr 1fr;
}

.about-me-box1-specs {
  display: grid;
  grid-template-rows: 5em 1fr;
}

.about-me-img {
  max-width:500px;
  max-height:500px;
  padding-left: 2em;
  -webkit-clip-path: ellipse(300px 400px at 100% 0%);
          clip-path: ellipse(300px 400px at 100% 0%);
}

.about-me-box2{
  display:grid;
  grid-template-rows: 1fr 2fr;
}
.about-me {
  padding-top:2em;
  font-size:x-large;
}

.about-me-text {
  display: flex;
  justify-content: center;
  padding-left: 4em;
}

.about-me-box2 p{
  display: flex;
  justify-content: center;
}

.canvas {
  width: 100%;
  height: 100%;
}

.email-img {
  max-width: 80px;
}

.inner-page-container{
  display:grid;
  grid-template-rows: 4em 30em .5fr 1fr
}

.github-img {
  padding-top:1em;
  width: 50px;
}

.linkedin-img {
  padding-top:1em;
width:60px;
}

.links-container {
  display: flex;
  justify-content: space-evenly;
}

.nav-bar{
  display:flex;
  justify-content: space-around;
  padding-top: 1em;
}

.nav-bar a {
  font-size: x-large;
}

.nav-container {
  padding-bottom: 2em;
}

.page-container{
  display:grid;
  grid-template-columns: 1fr 10fr 1fr;
  grid-template-rows: 1fr;
}

.projects-display{
  display:flex;
  flex-flow: column wrap;
}

.project-grid {
  display: grid;
  align-self: center;
  grid-column-gap: 1em;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  padding-bottom: 2em;
}

.projects-header {
  display:grid;
}

.project-header-h2 {
  display:flex;
  justify-content: center;
  padding-bottom: 1em;
}

.project-header-info {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-left: 1em;
  padding-bottom: 2em;
}

.project-display {
  width: 200px;
  height: 400px;
  object-fit: none;
  border:solid;
  border-color:black;
}

.project-title {
  display: flex;
  justify-content: center;
}

.resume-container{
  display: grid;
  align-self: center;
  justify-content: center;
}
.resume-h2{
  display:flex;
  justify-content:center;
  font-size: larger;
}
* {
  font: 700 16px "Open Sans", sans-serif; 
  color: #BF923C;
}
body {
  background-color: black;
}

.nav-container {
  background-color: rgba( 22, 28, 75);
}
.inner-page-container{
  background-color: #3C3C3C;
}
.indent-page{
    background-color:#1A1A1A;
}
*{
  margin:0;
  padding:0;
  box-sizing: border-box;
}

.modal-content {
position: fixed;
top: 0;
bottom: 0;
left: 0;
right: 0;

}

.modal-backdrop{
position: fixed;
top: 0;
bottom: 0;
left: 0;
right: 0;
z-index:100;
background-color: rgba(0,0,0,0.3);
}

.modal-box {
  position: relative;
  top: 10%;
  left: 12%;
  transform: -50%, -50%;
  min-height: 70%;
  width: 75%;
  overflow-y:auto;
  background-color:#1A1A1A;
  box-shadow: 0 0 10px rgba(0,0,0,0.25);
  z-index: 101;
  padding: 40px;
}
